<template>
    <div class="register-page input-border-none self-mt-header" :style="clientHeight">
		 <header class="post-head">
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">修改密码</span>
        </header>
		<!-- <mt-header title="修改密码" fixed>
            <router-link to="/qdsPersonal" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
		<div class="title-logo"></div>
        <div class="register-box">
            <el-form :model="registerForm" status-icon :rules="ruleRegister" ref="ruleRegister"  class="demo-ruleForm">
				<div class="items-line">
					<img :src="registerPwd" alt="">
					<el-form-item label="" prop="passWord">
                            <el-input type="password" v-model.trim="registerForm.oldPassWord" auto-complete="off" placeholder="请输入旧密码"></el-input>
                        </el-form-item>
				</div>
				<div class="items-line">
					<img :src="registerPwd" alt="">
					<el-form-item label="" prop="passWord">
                            <el-input type="password" v-model.trim="registerForm.passWord" auto-complete="off" placeholder="请输入新密码"></el-input>
                        </el-form-item>
				</div>
				<div class="items-line">
					<img :src="registerPwd" alt="">
					<el-form-item label="" prop="checkPass">
                            <el-input type="password" v-model.trim="registerForm.checkPass" auto-complete="off" placeholder="请确认密码"></el-input>
                        </el-form-item>
				</div>
            </el-form>              
    	</div>
		<div class="zhanwei"></div>
		<div class="login-btn-box">
			<span @click="setUserPwd">确认</span>
		</div>
        
             
            
    </div>

</template>

<script>
import Util from '@/js/mUtils';
import { Notify} from 'vant';
import loginTitle from "@/assets/qds-img/login-title.png";
import loginLock from "@/assets/qds-img/login-lock.png";
import loginCode from "@/assets/qds-img/login-code.png";
import registerPwd from "@/assets/qds-img/register_pwd.png";
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loginTitle: loginTitle,
			loginLock: loginLock,
			loginCode: loginCode,
			registerPwd: registerPwd,
			contentText:'',
			textShow: false,
            show: true,
            count: '',
            timer: null, 
            registerForm: {
                oldPassWord: "",
                passWord: "",
                checkPass: ""
            },
            ruleRegister: {
               
            },
            corpNameTipStyle: false,
            whetherRegister: 'exist',
            corpNameTipFont: '',
            loading: false,
            companyNameList: [],    // 搜索到的公司集合
            list: [{value: 0,label: '正在加载中'}], 
			checked: false,    // 是否同意用户协议
			clientHeight: {
				height: ""
			},
        };
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
    mounted() {},
    components: {
        // lHeader,
        // lFooter
    },
    methods: {
		 backHistory() {
            window.history.back(-1)
        },
		getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
		setUserPwd(rulesChangePwd){                                                  //确认修改密码
            var token = Util.getCookie('qdsMToken')
			var that = this
			if(that.registerForm.oldPassWord == '') {
				Notify({
					message: '请输入旧密码',
					duration: 3000,
					background: '#E6A23C'
				});
				return false
			}
			if(that.registerForm.passWord == '') {
				Notify({
					message: '请输入新密码',
					duration: 3000,
					background: '#E6A23C'
				});
				return false
			}
			if(that.registerForm.checkPass == '') {
				Notify({
					message: '请确认密码',
					duration: 3000,
					background: '#E6A23C'
				});
				return false
			}
			if(that.registerForm.checkPass != that.registerForm.passWord) {
				Notify({
					message: '两次输入密码不一致!',
					duration: 3000,
					background: '#E6A23C'
				});
				return false
			}
			var Expression = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/;   
            var objExp=new RegExp(Expression);
			if(!objExp.test(that.registerForm.checkPass) || !objExp.test(that.registerForm.passWord) || !objExp.test(that.registerForm.oldPassWord)) {
				Notify({
					message: '密码为不少于6位的数字和字母组成',
					duration: 3000,
					background: '#E6A23C'
				});
				return false
			}
			that.loading = true 
			that.axios(that.qdsGlobalApiUrl + 'modifyPasswordbyOldPwd?token=' + token, {
				method: 'post',
				params: {
					oldpwd: that.registerForm.oldPassWord,
					newPwd: that.registerForm.passWord
				},
				timeout: 10000
			})
			.then(function (response) {
				if(response.data.code == '200'){
					that.loading = false 
					Notify({
						message: '密码修改成功',
						duration: 3000,
						type:'success',
						background: '#E6A23C'
					});
				}else {
					that.loading = false 
					Notify({
						message: response.data.message,
						duration: 3000,
						type:'error',
						background: '#E6A23C'
					});
				}
			})
			.catch(function (error) {
				that.loading = false 
				Notify({
					message: '修改密码操作异常，请联系客服021-22876396',
					duration: 3000,
					background: '#FF6262'
				});
			});
        },
    },
        
      
}

</script>

<style scoped>
	.post-head{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2.6875rem;
        border-bottom: 1px solid #F5F5F5;
        background-color: #fff;
    }
    .back{
        position: absolute;
        left: .9375rem;
        display: inline-block;
        width: .5625rem;
        height: 1.1875rem;
        background: url(../../assets/post/back_icon.png);
        background-size: 100% 100%;
        cursor: pointer;
    }
    .head-font{
        font-size: 1rem;
        color: #0B231F;
        font-weight: bold;
    }  
	input {
		margin: 0;
		padding: 0;
		border: none;
	}
	img{
		vertical-align: middle;
		border: none;
	}
	.register-page {
		height: 100%;
		width: 100%;
		background: #fff;
	}
	.register-page .title-logo{
		height: 3.75rem;
	}
	.register-page .register-box {
		padding-top: .5rem;
		margin: 0 2rem;
	}
	.register-page .register-box .items-line {
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		height: 4rem;
		line-height: 4rem;
		border-bottom: 1px solid #E5E5E5;
	}
	.register-page .register-box .items-line img {
		width: .875rem;	
		padding-left: 20px;
	}

	.register-page .select-agreement{
		font-size: .75rem;
		padding: 1.4375rem 0 .6875rem 2.8125rem;
	}
	.register-page .select-agreement p input{
		display: none;
	}
	.register-page .select-agreement p .agreement_btn+label{
		display: inline-block;
		width: .8125rem;
		height: .8125rem;
		border: 1px solid gray;
		border-radius: 50%;
		
	}


	.register-page .login-btn-box {
		width: 80%;
		margin: 0 auto;
		text-align: center;
	}
	.register-page .login-btn-box span{
		display: inline-block;
		width: 100%;
		border-radius: 1.375rem;
		background: #2da1a4;
		line-height: 2.75rem;
		color: #fff;
		font-size: 1.0625rem;
	}
	.register-page .select-agreement .user-agreement{
		color: #2da1a4;
	}
	.zhanwei {
		height: 1.875rem;
	}
	.register-page .footer{
		letter-spacing: .1em;
		width:60% ;
		margin: 0 auto;
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		font-size: .8125rem;
		color: #818D8D;
		font-size: .8125rem;
	}
	.register-page .footer .go-login-btn{
		color: #2da1a4;
	}
	.open-text {
		width: 80%;
	}

</style>



